* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body,
html {
  height: 100%;
}
body {
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
}
.neon {
  border: none;
  background: transparent;
  font-family: 'Exo', sans-serif;
  color: beige;
  font-size: 3rem;
  opacity: 1;
  text-shadow: 0 0 40px beige;
}
.off {
  opacity: 0.2;
  text-shadow: none;
}
